import { GroundType } from "utils/types";

export interface SwitchProps {
  value: boolean;
  onChange: (value: boolean) => void;
  ground: GroundType;
}

const CustomSwitch: React.FC<SwitchProps> = ({ value, onChange, ground }) => {
  return (
    <div
      onClick={() => onChange(!value)}
      className={`h-28px w-16 relative transition-all flex items-center rounded-full cursor-pointer ${
        value ? "primaryBackground " : `input${ground}Background`
      }`}
    >
      <div className="p-1">
        <div
          className={`w-6 h-6 relative rounded-full bg-white transition transform ${
            value ? "translate-x-8" : "tenslate-x-0"
          }`}
        ></div>
      </div>
    </div>
  );
};

export default CustomSwitch;
